import { Link } from "gatsby"
import PropTypes from "prop-types"
import React, { useState, useEffect } from "react"
import { IconContext } from "react-icons"
import { FaBars } from 'react-icons/fa'
import "./header.css"

const makeBurger = () => {
  return(
    <span class="burgerBorder">
      <FaBars color={"white"} size={"1.5rem"}/>
    </span>
  )
}

const desktopHeader = () => {
  return (
    <header
      style={{
        marginBottom: `1.45rem`,
      }}
    >
      <div
        style={{
          margin: `0 auto`,
          padding: `0.15rem 0.rem`,
        }}
      >
        <h1 style={{ margin: 0 ,  display:`inline`, fontSize:'2.3rem'}}>
          <Link
            to="/"
            style={{
              color: `white`,
              textDecoration: `none`,
            }}
          >
            Yin Lin • <span lang="Chinese">林胤</span> 
          </Link>
        </h1>
        <ul style={{ display:`inline` }}>
            <li style={{ display:`inline` }}><a class="active" href="#home" style={{fontSize:'1.5rem'}}>Home</a></li>
            <li style={{ display:`inline` }}><a href="#news" style={{fontSize:'1.5rem'}} >News</a></li>
            <li style={{ display:`inline` }}><a href="#contact" style={{fontSize:'1.5rem'}} >Contact</a></li>
            <li style={{ display:`inline` }}><a href="#about" style={{fontSize:'1.5rem'}} >About</a></li>
        </ul>
      </div>
      <hr style={{
        color: 'white',
        backgroundColor: 'white',
        height: '0.15rem',
        borderColor : 'white'
        }}/>
    </header>
  )
}

const mobileHeader = () => {
  return (
    <header
      style={{
        marginBottom: `1.45rem`,
      }}
    >
      <div
        style={{
          margin: `auto`,
          padding: `0.15rem 0.rem`,
        }}
      >
        <h1 style={{ margin: 0 ,  display:`inline`, fontSize:'2.3rem'}}>
          <Link
            to="/"
            style={{
              color: `white`,
              textDecoration: `none`,
            }}
          >
            Yin Lin • <span lang="Chinese">林胤</span> 
          </Link>
        </h1>
        <ul style={{ display:`inline` }}>
            <li style={{ display:`inline`}}>{ makeBurger() }</li>
        </ul>
      </div>
    </header>
  )
}

class Header extends React.Component {
  render(props){
    if (this.props.isMobile)
      return(mobileHeader())
    else{
      return(desktopHeader())
    }
  }
}

Header.propTypes = {
  siteTitle: PropTypes.string,
}

Header.defaultProps = {
  siteTitle: ``,
}


export default Header
