/**
 * Layout component that queries for data
 * with Gatsby's useStaticQuery component
 *
 * See: https://www.gatsbyjs.org/docs/use-static-query/
 */

import React from "react"
import PropTypes from "prop-types"
import { StaticQuery, graphql } from "gatsby"
import Container from "../components/container"

import Header from "./header"
import "./layout.css"


class Layout extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      width: 1000, // default width
      isMobile: true
    }
  }
  render(props){
    const data = StaticQuery(graphql`
      query SiteTitleQuery {
        site {
          siteMetadata {
            title
          }
        }
      }
    `)

    return (
      <div style={{ maxWidth: this.state.width, margin: `0 auto`,}}>
        <Header isMobile={ this.state.isMobile }></Header>
        <div
          style={{
            margin: `0 auto`,
            padding: `0 1.0875rem 1.45rem`,
          }}
        >
          <main>
            {this.props.children}
          </main>
          <footer>
            © {new Date().getFullYear()}, Built with
            {` `}
            <a href="https://www.gatsbyjs.org">Gatsby</a>
          </footer>
        </div>
      </div>
    )
  }
  componentDidMount() {
    this.handleWindowSizeChange() // Set width
    window.addEventListener('resize', this.handleWindowSizeChange)
  }

  // make sure to remove the listener
  // when the component is not mounted anymore
  componentWillUnmount() {
    window.removeEventListener('resize', this.handleWindowSizeChange)
  }

  handleWindowSizeChange = () => {
    const desktopWidth = 960 // in px
    if (window.innerWidth < desktopWidth) {
      this.setState({ width: window.innerWidth-30, isMobile: true})
    } else {
      this.setState({ width: desktopWidth, isMobile: false})
    }
  }
}
Layout.propTypes = {
  children: PropTypes.node.isRequired,
}

export default Layout
